import React from "react";
import { useTranslation } from "react-i18next";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";
import Page from 'src/components/Page';
import MenuList from "./MenuList";
import MenuCheck from "./MenuCheck";
import { Container } from "@material-ui/core";



const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.default,
  }
}));

const HomeView = () => {
  const { t } = useTranslation("home");
  const classes = useStyles();
  const menuItems = [
    {
      id: 1,
      title: `${t("containerRegisterEquipment.titleSmall")}`,
      name: `${t("containerRegisterEquipment.titleMain")}`,
      route: "/equipments",
      icon: `/static/home/register.png`,
    },
    {
      id: 2,
      title: `${t("containerSurgicalInstruments.titleSmall")}`,
      name: `${t("containerSurgicalInstruments.titleMain")}`,
      route: "/sets",
      icon: `/static/home/document.png`,
    },
    {
      id: 3,
      title: `${t("containerHistory.titleSmall")}`,
      name: `${t("containerHistory.titleMain")}`,
      route: "/borrowing-history",
      icon: `/static/home/history.png`,
    },
    {
      id: 4,
      title: `User Management`,
      name: `จัดการ User`,
      route: "/userManagement",
      icon: `/static/home/history.png`, 
    },
  ];

  return (
    <Page className={classes.root} title="Home">
      <Container>
        {/* <Box textAlign="center" pt={2}>
          <Typography variant="h4" gutterBottom>
            {t("titleMain")}
          </Typography>
          <Divider />
        </Box> */}
        <Box pt={2}>
          <MenuCheck />
        </Box>
        <Box pt={2}>
          <MenuList menuList={menuItems} />
        </Box>
      </Container>
    </Page>
  );
};

export default HomeView;
