import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { Grid, makeStyles } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import clsx from 'clsx';
import PropTypes from 'prop-types';

const useStyles = makeStyles(theme => ({
  root: {},
  button: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  buttonCheck: {
    padding: '16px 32px'
  }
}));

const MenuCheck = ({ className, ...rest }) => {
  const classes = useStyles();
  const { t } = useTranslation('home');

  return (
    <Card className={clsx(className, classes.root)} {...rest}>
      <CardContent>
        <Grid container spacing={3}>
          <Grid item xs={12} sm={12} md={9}>
            <Typography
              variant="subtitle1"
              align="left"
              color="textSecondary"
              gutterBottom
            >
              {t('containerMedicalEquipment.titleSmall')}
            </Typography>
            <Typography variant="h6" align="left">
              {t('containerMedicalEquipment.titleMain')}
            </Typography>
          </Grid>
          <Grid item xs={12} sm={12} md={3} className={classes.button}>
            <Button
              component={Link}
              to={'/verify'}
              variant="contained"
              color="secondary"
              fullWidth={true}
              className={classes.buttonCheck}
            >
              {t('button.checkEquipment')}
            </Button>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

MenuCheck.propTypes = {
  className: PropTypes.string
};

export default MenuCheck;
